const App = () => {
  const [tyres, setTyres] = useState([]);
  const [errorMessage, setErrorMessage] = useState(null);
  var osto = [];
  var ostoID;

  useEffect(() => {
    tyreService.getAll().then((response) => {
      setTyres(response);
    });
  }, []);

  const ostaRenkaat = () => {
    const tyre = tyres.find((b) => b.id === ostoID.toString());
    if (
      window.confirm(
        `Olet ostamassa seuraavan rengassarjan: ${
          " Valmistaja: " +
          tyre.merkki +
          " *** Renkaan Malli: " +
          tyre.malli +
          " *** Renkaan tyyppi: " +
          tyre.tyyppi +
          " *** Koko: " +
          tyre.koko +
          " *** Hinta: " +
          tyre.hinta
        }. Oletko varma? `
      )
    ) {
      var id = tyre.id;
      console.log("tyre: ", tyre);
      const updatedTyre = {
        saldo: (tyre.saldo -= 1),
      };

      tyreService
        .update(id, updatedTyre)
        .then((res) => {
          setTyres(tyres.map((p) => (p.id !== tyre.id ? p : res)));
        })
        .catch((error) => {
          setErrorMessage(error.response.data.error);
          setTimeout(() => {
            setErrorMessage(null);
          }, 5000);
        });
    }
  };

  const SelectAndBuyTyres = (props) => {
    const [renkaanKoko, setRenkaanKoko] = useState("");
    var val4;

    const searchBy = (arr = [], searchKeys = [], value = "") => {
      return arr.filter((item) =>
        searchKeys.length
          ? searchKeys.some((key) =>
              (item[key] || "").toLowerCase().includes(value.toLowerCase())
            )
          : true
      );
    };

    if (renkaanKoko || renkaanKoko === "") {
      val4 = searchBy(props.tyres, ["koko"], renkaanKoko.vert);
    }

    const onRowSelect = (event) => {
      osto.push(event.row);
      ostoID = osto.map((i) => i.id);
    };

    const Filter = () => {
      const columns = [
        { field: "merkki", headerName: "Valmistaja", width: 120 },
        { field: "malli", headerName: "Malli", width: 165 },
        { field: "tyyppi", headerName: "Tyyppi", width: 90 },
        { field: "koko", headerName: "Renkaan koko", width: 130 },
        { field: "saldo", headerName: "Varastosaldo", width: 130 },
        { field: "hinta", headerName: "Hinta € / KPL", width: 140 },
      ];

      return (
        <div style={{ height: 350, width: 820 }}>
          <DataGrid
            className="tyresdata"
            style={{
              color: "white",
              fontFamily: "RSOH1",
              border: "4",
              borderColor: "white",
            }}
            rows={val4}
            columns={columns}
            pageSize={28}
            rowsPerPageOptions={[28]}
            checkboxSelection
            onRowClick={onRowSelect}
          />
        </div>
      );
    };

    function handleSelectKoko(data) {
      setRenkaanKoko(data);
    }

    const koot = [
      { value: "---renkaan koko---", label: "---renkaan koko---", vert: "" },
      { value: "14", label: "14", vert: "-14" },
      { value: "15", label: "15", vert: "-15" },
      { value: "16", label: "16", vert: "-16" },
      { value: "17", label: "17", vert: "-17" },
      { value: "18", label: "18", vert: "-18" },
      { value: "19", label: "19", vert: "-19" },
    ];

    return (
      <div className="selectandbuytyres">
        <Box
          border={2}
          borderRadius={2}
          borderColor={"black"}
          width={"100%"}
          height={"100%"}
          style={{ backgroundColor: "#F205B3" }}
        >
          <div className="flexrow">
            <div
              className="dropdown-container"
              style={{ fontSize: 18, marginLeft: 40, marginBottom: 30 }}
            >
              <h2>ALOITA TÄSTÄ!!</h2>
              Valitse renkaan koko
              <Select
                className="black"
                options={koot}
                placeholder="---renkaan koko---"
                value={renkaanKoko}
                onChange={handleSelectKoko}
              />
            </div>
            <div
              className="white"
              style={{ marginLeft: 120, fontSize: 18, marginBottom: 30 }}
            >
              <h2>
                VALITSE TÄSTÄ{" "}
                <Button
                  variant="contained"
                  style={{
                    marginLeft: 20,
                    width: 200,
                    height: 50,
                    fontSize: 18,
                    fontFamily: "RSOH1",
                  }}
                  onClick={ostaRenkaat}
                >
                  OSTA RENKAAT
                </Button>
              </h2>
              <Filter />
            </div>
            <div
              style={{
                marginLeft: 50,
              }}
            >
              <div className="white" style={{ marginLeft: 80, fontSize: 18 }}>
                <h2>LÖYDÄ PERILLE</h2>
              </div>
              <img
                style={{
                  marginLeft: 80,
                  border: 5,
                  borderColor: "white",
                  borderRadius: 10,
                }}
                className="logopicture"
                src={kartta}
                width={450}
                height={350}
                title="kartta"
                alt="kartta"
                justifycontent="left"
              />
            </div>
          </div>
        </Box>
        <div className="boxrow">
          <Box
            border={2}
            borderRadius={2}
            borderColor={"black"}
            width={600}
            height={450}
            marginTop={2}
            style={{ backgroundColor: "#F205B3", marginLeft: 15 }}
          >
            <h2 className="white" style={{ padding: 15, marginLeft: 30 }}>
              NÄILLÄ OHJEILLA RENKAANVAIHTO SUJUU!!
            </h2>
            <iframe
              src="https://www.youtube.com/embed/WWIVmcXL-Is"
              frameBorder="0"
              allow="autoplay; encrypted-media"
              allowFullScreen
              title="video"
              style={{
                marginLeft: 50,
                width: 500,
                height: 300,
              }}
            />
          </Box>
          <Box
            border={2}
            borderRadius={2}
            borderColor={"black"}
            width={600}
            height={450}
            marginTop={2}
            style={{ backgroundColor: "#F205B3", marginLeft: 20 }}
          >
            <div>
              <img
                style={{
                  marginLeft: 70,
                  marginTop: 50,
                  borderColor: "white",
                  borderRadius: 10,
                }}
                className="logopicture"
                src={varasLahto}
                width={450}
                height={350}
                title="varaslahto"
                alt="varaslahto"
                justifycontent="left"
              />
            </div>
          </Box>
          <Box
            border={2}
            borderRadius={2}
            borderColor={"black"}
            width={600}
            height={450}
            marginTop={2}
            style={{ backgroundColor: "#F205B3", marginLeft: 20 }}
          >
            <div>
              <img
                style={{
                  marginLeft: 75,
                  marginTop: 50,
                  borderColor: "white",
                  borderRadius: 10,
                }}
                className="logopicture"
                src={renkaanVaihto}
                width={450}
                height={350}
                title="renkaanvaihto"
                alt="renkaanvaihto"
                justifycontent="left"
              />
            </div>
          </Box>
        </div>
      </div>
    );
  };

  const hook = () => {
    axios.get("api/tyres");
  };

  useEffect(hook, []);

  return (
    <div style={{ backgroundImage: `url(${wholeBack})` }}>
      <Notification message={errorMessage} />
      <Header />
      <Banner />
      <SelectAndBuyTyres tyres={tyres} />
      <Footer />
    </div>
  );
};

export default App;
